.my-component {
    position: relative;
    background: linear-gradient(to bottom, darkblue 50%, lightgrey 50%);
    flex-grow: 1;
    overflow-y: auto;
}

.circle2 {
    width: 100px;
    height: 100px;
    background-color: lightgrey;
    border-radius: 50%;
    position: absolute;
    top: 50%; /* aligned with the intersection */
    left: 60%;
    transform: translate(-50%, -50%);
    z-index: 1;
}

.content {
    position: relative;
    z-index: 2;
    padding: 20px;
}
