.inner-containers-parent {
    display: flex;
    justify-content: space-between;
    width: calc(100% - var(--side-margin) * 2);
    position: absolute;
    top: var(--vertical-position);
    left: var(--side-margin);
    right: var(--side-margin);
    height:8%;
}

.inner-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45%;
    height: 145%;
    border-radius: 15px;
    border: 1px solid #000;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: color 0.3s, border-color 0.3s;
    background-color: transparent; /* Ensure container background is transparent */
    --percentage: 0%; /* Default to no fill */
}

    .inner-container::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: var(--percentage);
        height: 100%;
        background-color: lightgrey; /* Fill specified percentage with light grey */
        z-index: -1; /* Ensure background is behind content */
        transition: width var(--animation-duration) ease-out;
    }

    .inner-container > * {
        position: relative;
        z-index: 1; /* Ensures content is above the background */
    }

    .inner-container::after {
        content: attr(data-content);
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transition: opacity 0.3s ease-in-out;
        opacity: 0;
    }

    .inner-container[data-content]:not(:empty)::after {
        opacity: 1;
        transition-delay: 0.3s; /* Delay to match content fade-in after fade-out */
    }

    .inner-container:hover {
        background-color: black;
        color: dimgray;
        border-color: white;
        z-index:0;
    }


@media (max-width: 650px) {


    .inner-containers-parent {
        width: calc(85% - var(--side-margin) * .5);
        left: calc(var(--side-margin) * .5);
        right: calc(var(--side-margin) * .5);
    }
}
