.transparent-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid #000;
    border-radius: 85px;
    padding: 20px;
    background-color: transparent;
    width: 80%;
    max-width: 900px;
    margin: auto;
    position: absolute;
    height: 400px;
    top: 1200px; /* Adjust this value to move the container up or down */
    left: 50%; /* Centers the container horizontally */
    transform: translate(-50%, -10%); /* Adjusts for centering horizontally and fine-tunes vertical position */
}


@media (max-width: 650px) {
    .transparent-container {
        border-radius: 55px;
    }
}

