.backgroundComponent {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background-image: linear-gradient(to bottom, rgba(0, 0, 139, 0) 3100px, rgba(0, 0, 0, 0.12) 3200px, rgba(0, 0, 139, 0) 3200px),
        linear-gradient(to bottom, lightgrey 0%, lightgrey 3200px, darkblue 3200px, darkblue 100%);
    overflow: hidden;
    z-index: 1;
}

.circle {
    width: 100px; /* Circle diameter */
    height: 100px; /* Circle diameter */
    border-radius: 50%; /* Makes it a circle */
    background-color: darkblue; /* Circle color */
    position: absolute;
    z-index:-1;
}

.animate {
    animation: slideIn 1s forwards;
}

@keyframes slideIn {
    to {
        transform: translateX(var(--slideDistance));
    }
}
