.circles-row-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    bottom: 80%; /* Adjust based on container's height */
    width: calc(100% - var(--circles-row-side-margin) * 2.6); /* Apply side margin */

    height: auto;

}

.circle-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px; /* Circle size */
    height: 40px; /* Circle size */
    border-radius: 50%;
    border: 1px solid #000; /* Adjust based on container's border */
    background-color: white; /* Set the background of the circles */
    font-size: 14px; /* Adjust as needed */
    color: black; /* Initial color for the number */
    transition: background-color 0.3s, color 0.3s, border-color 0.3s; /* Smooth transition for hover effect */
}

    .circle-container:hover {
        background-color: #000; /* Invert the background color */
        color: white; /* Invert the number color */
        border-color: white; /* Invert the border color */
    }




@media (max-width: 650px) {
    .circles-row-container {
        width: calc(100% - var(--circles-row-side-margin)* 0.7);
    }

}