.fancy-rectangle-container {
    display: flex;
    justify-content: flex-start; /* Align items to the left */
    align-items: center;
    margin-top: 100px; /* Custom vertical positioning; adjust as needed */

    position: absolute; /* For sliding animation */
    left: -100%; /* Start off-screen to the left */
    transition: left 0.5s ease-in-out; /* Smooth transition for sliding */
}

.slide-in {
    left: 0; /* Slide in to make the whole component visible */
}

.slide-out {
    left: -600px; /* Adjust this value based on the width of your main rectangle to only show the smaller rectangle */
}

.main-rectangle {
    position: relative;
    width: 600px; /* Adjust size as needed */
    height: 400px; /* Adjust size as needed */
    background-color: lightgray;
    border-radius: 10px; /* Rounded corners */
}

.smaller-rectangle {
    position: absolute;
    right: -80px; /* Adjust based on the main rectangle size */
    top: 10%; /* 70% height of the parent */
    width: 15%; /* 20% of the parent's width */
    height: 15%; /* 20% of the parent's height */
    background-color: lightgray;
    border-radius: 5px; /* Adjust based on the smaller rectangle size */
}

.envelope-image {
    width: 80%; /* Adjust as needed */
    height: auto;
}

.shake {
    animation: shake 1s ease-in-out; /* Adjust duration here if needed */
}

@keyframes shake {
    0%, 100% {
        transform: rotate(0deg);
    }

    25% {
        transform: rotate(-10deg);
    }

    75% {
        transform: rotate( 10deg);
    }
}


@media (max-width: 1200px) {


    .main-rectangle {
        position: relative;
        width: 80vw; /* Adjust size as needed */
        height: 60vw; /* Adjust size as needed */
        background-color: lightgray;
        border-radius: 10px; /* Rounded corners */
    }

    .smaller-rectangle {
        position: absolute;
        right: -13%; /* Adjust based on the main rectangle size */
        top: 10%; /* 70% height of the parent */
        width: 15%; /* 20% of the parent's width */
        height: 15%; /* 20% of the parent's height */
        background-color: lightgray;
        border-radius: 5px; /* Adjust based on the smaller rectangle size */
    }

    .slide-out {
        left: -80vw; /* Adjust this value based on the width of your main rectangle to only show the smaller rectangle */
    }

}